.popup__pieces {
  position: absolute;
  left: 0;
  top: 0;
  width: 97%;
  height: 90%;
}

.popup__piece {
  float: left;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0s 0s;
}

.popup.s--closed .popup__piece {
  transition: transform 0.7s 0.1s cubic-bezier(0.69, -0.47, 0.97, 1.04);
}

.popup__piece:after {
  content: "";
  display: table;
  clear: both;
}

.popup__piece-inner {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(235, 89, 92, 0.803);
  opacity: 0;
  transition: opacity 0.28s 0.55s ease-in, transform 0.7s 0.1s ease-out;
}

.popup.s--active .popup__piece-inner {
  transition: opacity 0.35s, transform 0.7s ease-in-out;
  transform: translate3d(0, 0, 0) rotateX(0) rotateY(0) !important;
  opacity: 1;
}

.popup__content {
  position: relative;
  height: 90vh;
  padding: 30px;
  background: url(./hhh.jpg);
  color: sienna;
  transition: opacity 0.2s;
  opacity: 0;
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  /* background-size: contain; */
  /* background-repeat: no-repeat; */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.popup.s--active .popup__content {
  transition-delay: 0.6s;
  opacity: 1;
}

.popup__close {
  position: absolute;
  right: 30px;
  top: 30px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  font-weight: bold;
}

.popup__close:before,
.popup__close:after {
  content: "";
  position: absolute;
  left: 0;
  top: 14px;
  width: 100%;
  height: 2px;
  background: red;
}

.popup__close:before {
  transform: rotate(45deg);
}

.popup__close:after {
  transform: rotate(-45deg);
}

.popup__heading {
  font-family: fantasy;
  margin-bottom: 20px;
  font-size: 50px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.popup__text {
  font-family: "Times New Roman", Times, serif;
  font-size: 60px;
  line-height: 1.5;
  text-align: center;
  width: 100%;
  color: #d45c75;
}

.popup-btn {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 200px;
  height: 50px;
  margin-left: -100px;
  margin-top: -25px;
  background: transparent;
  outline: none;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: bold;
  color: #000;
  border: 2px solid #000;
  transition: all 0.3s;
  cursor: pointer;
}

.popup-btn:hover {
  background-color: #000;
  color: #fff;
  letter-spacing: 2px;
}
@media only screen and (max-width: 1280px) {
  .popup__text {
    font-family: "Times New Roman", Times, serif;
    font-size: 30px;
    line-height: 1.5;
    text-align: center;
    width: 100%;
    color: #d45c75;
  }
  .popup__content {
    position: relative;
    height: 100vh;
